*
{
    margin: 0;
    padding: 0;
}

html,
body
{
    height: 100vh;
    font-family: 'Poppins';
    color: white;
}

.webgl
{
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}

.container{
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100vh;
    display: grid;
    place-content: center;
}
.content{
    display: flex;
    gap: 5em;
    width: 100%;
    padding-top: 3rem;
    position: relative;
}
.content:before{
    content:' ';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    border-bottom: 2px solid white;
    transform: scaleX(1);
}

h1 {
    font-size: 5rem;
    width: 50vw;
    line-height: 97%;
    text-align: right;
}

h1, p {
    flex-basis: 0;
    flex: 1;
    clip-path: polygon(0 0, 100% 0,100% 0, 0 0);
}

p{
    font-size: 1.5rem;
    width: 40vh;
}